@layer base {
  html {
    color-scheme: light dark;
  }
  body {
    @apply bg-white text-gray-900 dark:bg-gray-900 dark:text-gray-100;
    font-family: var(--font-tajawal), system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }
  /* Apply Tajawal font for RTL content */
  .rtl {
    font-family: var(--font-tajawal);
  }
  .ltr {
    font-family: var(--font-tajawal);
  }
  h1 {
    @apply text-4xl font-bold;
    color: var(--arabic-black);
    margin-bottom: 1rem;
  }
  h2 {
    @apply text-3xl font-semibold;
    color: var(--arabic-black);
    margin-bottom: 0.75rem;
  }
  h3 {
    @apply text-2xl font-semibold;
    color: var(--arabic-black);
    margin-bottom: 0.75rem;
  }
  h4 {
    @apply text-xl font-semibold;
    color: var(--arabic-black);
    margin-bottom: 0.5rem;
  }
  h5 {
    @apply text-lg font-semibold;
    color: var(--arabic-black);
    margin-bottom: 0.5rem;
  }
  h6 {
    @apply text-base font-semibold;
    color: var(--arabic-black);
    margin-bottom: 0.5rem;
  }
  p {
    @apply text-base;
    color: var(--gray-700);
    margin-bottom: 1rem;
  }
  a {
    color: var(--arabic-green);
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
    color: var(--arabic-green-dark);
  }
  strong {
    @apply font-semibold;
  }
  /* Button styles */
  button, .btn {
    @apply rounded-md px-4 py-2 font-medium;
  }
  .btn-primary {
    background-color: var(--arabic-green);
    color: white;
    transition: background-color 0.2s ease;
  }
  .btn-primary:hover {
    background-color: var(--arabic-green-dark);
  }
  .btn-secondary {
    @apply bg-gray-200 transition-colors duration-200;
    color: var(--arabic-black);
  }
  .btn-secondary:hover {
    @apply bg-gray-300;
  }
  .btn-outline {
    background-color: transparent;
    color: var(--arabic-green);
    border: 1px solid var(--arabic-green);
    transition: background-color 0.2s ease, color 0.2s ease;
  }
  .btn-outline:hover {
    background-color: var(--arabic-green);
    color: white;
  }
  @media (prefers-color-scheme: dark) {
    h1, h2, h3, h4, h5, h6 {
      color: var(--gray-100);
    }
    p {
      color: var(--gray-300);
    }
    a {
      color: var(--arabic-green-light);
    }
    a:hover {
      color: var(--arabic-green);
    }
  }
}
